<!-- DOM Tag Name-->
<svelte:options tag="jse-asteroids"/>
<!-- xDOM Tag Name-->

<div id="JSE-game" on:click="{captchaClick}" on:mousemove="{moveSpaceship}" on:touchmove="{moveSpaceship}">
	{#each gameElement as ele, i}
		<img on:click|once="{() => smash(i)}" class:active="{ele.smashed}" draggable="{draggable}" class="gfx" style="transform: rotate({ele.r}deg); top: {ele.y}px; left: {ele.x}px;" src="{ele.src}" alt="" />
	{/each}
</div>


<script>
	import { onMount, createEventDispatcher } from 'svelte';

	//Events
	const dispatch = createEventDispatcher();

	//Data model
	const mlData = { mouseClicks:0 };

	//GFX
	const spaceShipIco = 'data:image/png;base64,';
	const asteroidSource = 'data:image/png;base64,';
	const asteroidFlame = 'data:image/png;base64,';
	
	//force draggable false
	const draggable = false;

	//Timer
	let update = null;

	//game elements
	const gameElement = [{
			src: spaceShipIco,
			x: 20,
			y: 120,
			r: 45,
			type: 'spaceShip',
		},{
			src: asteroidSource,
			x: 230,
			y: 20,
			r: 0,
			type: 'asteroid',
			smashed: false,
		},{
			src: asteroidSource,
			x: 230,
			y: 120,
			r: 0,
			type: 'asteroid',
			smashed: false,
		},{
			src: asteroidSource,
			x: 130,
			y: 70,
			r: 0,
			type: 'asteroid',
			smashed: false,
		}];

	//smash android
	const smash = (i) => {
		gameElement[i].smashed = true;
		gameElement[i].src = asteroidFlame;
		
		captchaClick();

		if ((gameElement[1].smashed) && (gameElement[2].smashed) && (gameElement[3].smashed)) {
			gameCompleted();
			clearInterval(update);
		}
	};

	//move spaceship
	const moveSpaceship = (e) => {
		const rect = e.currentTarget.getBoundingClientRect();
		const mouseX = e.pageX - rect.left;
		const mouseY = e.pageY - rect.top;

		gameElement[0].r = Math.atan2(mouseY - gameElement[0].y, mouseX - gameElement[0].x) * (180 / Math.PI) + 85;
	};

	const draw = () => {
		gameElement[1].x -= 6;
		if (gameElement[1].x <= 0) gameElement[1].x = 290;
		gameElement[1].r += 5;

		gameElement[2].y -= 3;
		if (gameElement[2].y <= 0) gameElement[2].y = 190;
		gameElement[2].r -= 3;

		gameElement[3].x -= 3;
		gameElement[3].y -= 3;
		if (gameElement[3].x <= 0 && gameElement[3].y <= 0) {
			gameElement[3].x = 230;
			gameElement[3].y = 190;
		}
		gameElement[3].r += 4;
	}

	update = setInterval(draw, 100);

	//Game complete
	const gameCompleted = () => {
		mlData.finishTime = new Date().getTime();
		dispatch('complete', mlData);
	};

	//collect clicks
	const captchaClick = () =>{
		mlData.mouseClicks += 1;
	};
</script>

<style>
#JSE-game {
	height:100%;
	background:#000;
	cursor: url('data:image/png;base64,'), auto;
}

.gfx {
	position:absolute;
	opacity:1;
	transition: opacity 0.6s;
}

.gfx.active {
	opacity:0;
}
</style>
